/* LoginPage.css */

.loginPageContainer {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.loginForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  gap: 25px;
  width: 100%;
}


.formField {
  width: 350;
  margin-top: 25px; 
}
.formLabel {
  font-size: 18px;
  margin-bottom: 10px;
  display: block; /* Кожен label розміщується на новому рядку */
}

.formInput {
  padding: 12px;
  font-size: 16px;
  width: 350px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid #ccc; /* Додайте рамку для вигляду поля вводу */
}

.formButton {
  width: 200px;
  padding: 15px;
  font-size: 18px;
  background-color: #2ecc71;
  color: white;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.formButton:hover {
  background-color: #27ae60;
}

.formErrorText {
  font-size: 14px;
  color: red;
  margin-top: 5px; /* Збільште відстань від тексту помилки до поля вводу */
}
